/*
KISSY CSS Reset

1. 2.
*/

*,
body,
html {
    margin: 0px;
    padding: 0px;
    font-family: 'MyriadSetPro-Thin', calibri, Helvetica, "Microsoft YaHei",Arial, "\5B8B\4F53", sans-serif;
}

div,
span,
dl,
dt,
dd,
ul,
ol,
li,
fieldset,
lengend,
button,
input,
pre,
address,
cite,
dfn,
em,
var,
del,
p,
blockquote {
    font-size: 16px;
}

a{outline:none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

/*  */

body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
fieldset,
lengend,
button,
input,
textarea,
th,
td {
    /* table elements  */
    margin: 0;
    padding: 0;
}

/*  */

body,
button,
input,
select,
textarea {
    /* for ie */
    font: 12px/1 'MyriadSetPro-Thin', calibri, Helvetica, "Microsoft YaHei",Arial, "\5B8B\4F53", sans-serif;
}

h1 {
    font-size: 18px;
    /* 18px / 12px = 1.5 */
}

h2 {
    font-size: 16px;
}

h3 {
    font-size: 14px;
}

h4,
h5,
h6 {
    font-size: 100%;
}

address,
cite,
dfn,
em,
var {
    font-style: normal;
}

/*  */

code,
kbd,
pre,
samp,
tt {
    font-family: "Courier New", Courier, monospace;
}

/*  */

small {
    font-size: 12px;
}

/*  12px  small  */

/*  */

ul,
ol {
    list-style: none;
}

/*  */

a {
    text-decoration: none;
}

abbr[title],
acronym[title] {
    /* 1.ie6  abbr; 2.ie6  */
    border-bottom: 1px dotted;
    cursor: help;
}

q:before,
q:after {
    content: '';
}

/*  */

legend {
    color: #000;
}

/* for ie6 */

fieldset {
    border: none;
}

/* img  img  */

/*Img border in a's and image quality*/

img {
    height: auto;
    border: 0;
    -ms-interpolation-mode: bicubic;
    image-rendering: optimizeQuality;
    /* Firefox 3.6+ */
    vertical-align: middle;
}

/* optgroup  */

button,
input,
select,
textarea {
    outline: none;
    border: none;
    font-size: 100%;
    background: transparent;
    /*  ie  */
}

/*  */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

/*  hr */

hr {
    border: none;
    height: 1px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none !important;
    margin: 0;
}

@font-face {
    font-family: 'iconfont';  /* project id 420665 */
    src: url(../../static/fonts/iconfont.c15c0d3.eot),
     url(../../static/fonts/iconfont.c15c0d3.eot?#iefix) format('embedded-opentype'),
     url(../../static/fonts/iconfont.bc77fa5.woff2) format('woff2'),
     url(../../static/fonts/iconfont.a7a8b9d.woff) format('woff'),
     url(../../static/fonts/iconfont.9bc99d3.ttf) format('truetype'),
     url(../../static/img/iconfont.269b19a.svg#iconfont) format('svg');
}

.iconfont {
    font-family: "iconfont" !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
}

.clearfix:after {
    display: block;
    clear: both;
    content: "";
    visibility: hidden;
    height: 0;
}

.clearfix {
    zoom: 1;
}
